<template>
    <div class="others">
        <section class="x-spread">
            <h1 class="title" ref="spread">Spread 动画</h1>
            <p class="des">
                用于提供水平和垂直方向展开收起动画功能的小组件。
            </p>
            <h2>基础用法</h2>
            <div class="container">
                <div class="example">
                    <x-button wave @click="visible1=!visible1" style="margin-bottom:10px;">纵向展开 / 收起</x-button>
                    <x-spread :visible="visible1">
                        <div style="width:400px;height:200px;background:#36b1bf;"></div>
                    </x-spread>
                    <br>
                    <x-button wave @click="visible2=!visible2" style="margin-bottom:10px;">横向展开 / 收起</x-button>
                    <x-spread :visible="visible2" horizontal>
                        <div style="width:400px;height:200px;background:#36b1bf;"></div>
                    </x-spread>
                </div>
                <pre class="code">
<span class="str">纵向动画</span>
<span class="tag">&lt;x-spread</span> <span class="key">:visible</span><span class="tag">=</span><span class="str">"visible1"</span><span class="tag">&gt;&lt;/x-spread&gt;</span>
<span class="str">横向动画</span>
<span class="tag">&lt;x-spread</span> <span class="key">:visible</span><span class="tag">=</span><span class="str">"visible2"</span> <span class="key">horizontal</span><span class="tag">&gt;&lt;/x-spread&gt;</span></pre>
            </div>
            <h1>Attributes</h1>
            <div class="attr">
                <table>
                    <thead>
                        <tr>
                            <th>参数</th>
                            <th class="des">说明</th>
                            <th class="type">类型</th>
                            <th>可选值</th>
                            <th>默认值</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>visible</td>
                            <td class="des">控制内容的展开和收起，可动态绑定，必填。组件内部使用 v-show 控制内容的显示</td>
                            <td class="type">Boolean</td>
                            <td>——</td>
                            <td>——</td>
                        </tr>
                        <tr>
                            <td>horizontal</td>
                            <td class="des">横向展开和收起，默认为纵向。</td>
                            <td class="type">Boolean</td>
                            <td>——</td>
                            <td>false</td>
                        </tr>
                        <tr>
                            <td>duration</td>
                            <td class="des">动画持续时间，单位为 ms</td>
                            <td class="type">String , Number</td>
                            <td>——</td>
                            <td>300</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </section>
        <section class="x-spin">
            <h1 class="title" ref="spin">Spin 加载中</h1>
            <p class="des">
                用于页面和区块的加载中状态。
            </p>
            <h2>基础用法</h2>
            <div class="container">
                <div class="example" style="display:flex;justify-content:flex-start;height:122px;">
                    <div class="spinning-controller">
                        <x-button @click="spinning=!spinning">{{spinning?'取消加载':'点击加载'}}</x-button>
                    </div>
                    <div>
                        <x-spin :spinning="spinning" tips="loading..." :width="100"></x-spin>
                    </div>
                </div>
                <pre class="code">
<span class="tag">&lt;x-spin</span> <span class="key">:spinning</span><span class="tag">=</span><span class="str">"spinning"</span> <span class="key">tips</span><span class="tag">=</span><span class="str">"loading..."</span> <span class="key">:width</span><span class="tag">=</span><span class="str">"100"</span><span class="tag">&gt;&lt;/x-spin&gt;</span>
</pre>
            </div>
            <h1>Attributes</h1>
            <div class="attr">
                <table>
                    <thead>
                        <tr>
                            <th>参数</th>
                            <th class="des">说明</th>
                            <th class="type">类型</th>
                            <th>可选值</th>
                            <th>默认值</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>spinning</td>
                            <td class="des">是否显示加载效果，默认不显示</td>
                            <td class="type">Boolean</td>
                            <td>——</td>
                            <td>false</td>
                        </tr>
                        <tr>
                            <td>delay</td>
                            <td class="des">延迟显示加载效果出现的时间，单位为 ms，在加载效果出现前修改 spinning 为 false，则不出现加载效果（防止闪烁）</td>
                            <td class="type">Number</td>
                            <td>——</td>
                            <td>——</td>
                        </tr>
                        <tr>
                            <td>width</td>
                            <td class="des">加载动画的直径大小，单位为 px</td>
                            <td class="type">Number</td>
                            <td>——</td>
                            <td>80</td>
                        </tr>
                        <tr>
                            <td>tips</td>
                            <td class="des">加载状态的提示文字，将出现在加载动画的正下方</td>
                            <td class="type">String</td>
                            <td>——</td>
                            <td>——</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </section>
        <x-linker :back="{path:'components/viewport/popover',text:'视图'}"></x-linker>
    </div>
</template>

<script>
    import mixin from '@/mixin/mixin.js'
    export default {
        name: 'Others',
        mixins: [mixin],
        data() {
            return {
                visible1: true,
                visible2: true,
                selected: '2-1',
                spinning: true,
                timerId: null
            }
        },
        watch: {
            spinning2(val) {
                if (val) {
                    this.timerId = setTimeout(() => {
                        this.spinning2 = false
                    }, 3000)
                }
            }
        },
        beforeDestroy() {
            this.timerId && window.clearTimeout(this.timerId)
        }
    }
</script>

